@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,700&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  scroll-behavior: smooth;
  font-family: "Open Sans", sans-serif;
  line-height: 1.5;
}

.header {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#ce40b4), to(#e974db));
  background-image: -webkit-linear-gradient(#ce40b4, #e974db);
  background-image: linear-gradient(#ce40b4, #e974db);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 24px 60px;
}

@media only screen and (max-width: 480px) {
  .header {
    padding: 0 12px 60px;
  }
}

.logo {
  font-size: 24px;
  font-weight: bold;
  margin: 0;
  padding: 0;
  color: #fff;
  letter-spacing: -1.8px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
}

.nav {
  display: flex;
  align-items: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.link {
  display: block;
  padding: 0 12px;
  text-decoration: none;
}

.girl {
  display: block;
  font-size: 64px;
  margin-bottom: -19px;
}

.fab {
  font-size: 28px;
  color: rgba(255, 255, 255, 0.6);
  -webkit-transition: 0.3s color ease-in-out;
  transition: 0.3s color ease-in-out;
}

.fab:hover {
  color: rgba(255, 255, 255, 1);
}

.fas {
  font-size: 28px;
  color: rgba(255, 255, 255, 0.6);
  -webkit-transition: 0.3s color ease-in-out;
  transition: 0.3s color ease-in-out;
  padding: 0 12px;
}

.fas:hover {
  color: rgba(255, 255, 255, 1);
}

@media only screen and (max-width: 480px) {
  .girl {
    visibility: hidden;
    font-size: 0;
    height: 96px;
  }

  .fab,
  .fas {
    font-size: 18px;
  }
}

.content {
  padding: 40px 58px 40px 40px;
  background-color: #fff;
  margin: -60px 24px 24px;
  border-radius: 4px;
  height: calc(150vh - 173px);
}

.readMore {
  display: block;
  text-align: center;
  margin-top: 48px;
}

.title {
  font-size: 32px;
  margin: 0;
  letter-spacing: -1.8px;
}

.content p {
  line-height: 1.5;
}

.content a {
  color: #ce40b4;
  font-weight: bold;
  text-decoration: none;
}

.content a:hover {
  text-decoration: underline;
}

.arrow {
  padding-top: 48px;
  text-align: center;
}

@media only screen and (max-width: 480px) {
  .content {
    margin: -60px 12px 24px;
    padding: 40px 50px 40px 40px;
  }
}

.myWork {
  padding: 20px 128px 64px;
  background-color: #fbfbfb;
}

@media only screen and (max-width: 480px) {
  .myWork {
    padding: 20px 20px 64px;
  }
}

.titlework {
  text-align: center;
  padding: 40px;
}

.grid {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  grid-column-gap: 64px;
  grid-row-gap: 64px;
  justify-content: center;
}

@media only screen and (max-width: 480px) {
  .grid {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  }
}

/* min? */
@media only screen and (min-width: 900px) {
  .grid {
    grid-template-columns: repeat(auto-fill, minmax(500px, 1fr));
  }
}

.card {
  height: 100%;
  overflow: hidden;
  background-color: white;
  box-shadow: 0 4px 8px 0 #e8e8e8;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-radius: 5px;
}

.card:hover {
  box-shadow: 0 8px 16px 0 #e8e8e8;
}

.card__text {
  padding: 24px;
}

.card__link {
  display: flex;
  align-items: center;
  color: #444444;
  font-weight: bold;
  text-decoration: none;
  padding: 0px 0 10px 0;
}

.card__link .fab {
  margin-right: 8px;
}

.card__img-container {
  position: relative;
  padding-top: 56.25%;
  overflow: hidden;
}

.card__img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: auto;
}

.siteIcon {
  padding: 0 8px 0 0;
}

.skills {
  background-color: #fff;
}

.skills__title {
  text-align: center;
  padding: 40px;
}

.skills p {
  text-align: center;
  line-height: 1.5;
  padding: 0 60px;
  margin: 0;
}

.skills__chart {
  text-align: center;
  padding: 64px;
}

.skills__chart svg {
  width: 100%;
  height: 85vh;
}

@media only screen and (max-width: 480px) {
  .skills__chart svg {
    height: auto;
  }
}

/* Animation */
.line {
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  -webkit-animation: dash 5s linear alternate infinite;
          animation: dash 5s linear alternate infinite;
}

@-webkit-keyframes dash {
  from {
    stroke-dashoffset: 1000;
  }
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes dash {
  from {
    stroke-dashoffset: 1000;
  }
  to {
    stroke-dashoffset: 0;
  }
}

.getInTouch {
  text-align: center;
  padding: 40px;

  background-color: #d458bd;
  height: calc(100vh);
}

.getInTouch__title {
  padding: 40px 40px 12px;
  color: white;
}

.social {
  display: inline-block;
  padding: 10px;
  margin: 0 auto 24px;
}

form {
  text-align: center;
}

input[type="text"],
textarea {
  font-size: 15px;
  font-family: inherit;
  font-weight: bold;
  color: #555;
  display: block;
  width: 100%;
  height: 40px;
  padding: 12px;
  border: 1px solid transparent;
  border-radius: 4px;
  box-sizing: border-box;
  resize: vertical;
  margin: 12px 0;
  -webkit-transition: top, box-shadow 0.2s ease-in;
  transition: top, box-shadow 0.2s ease-in;
  position: relative;
}

input[type="text"]:focus,
textarea:focus {
  box-shadow: 6px 6px 0 rgba(0, 0, 0, 0.3);
  top: -2px;
  outline: none;
}

.btn {
  font-size: 18px;
  padding: 12px;
  background-color: #d458bd;
  color: #fff;
  min-width: 180px;
  border: 3px solid #fff;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 12px;
  position: relative;
  font-weight: bold;
  -webkit-transition: box-shadow 0.2s ease-in;
  transition: box-shadow 0.2s ease-in;
}

.btn:hover,
.btn:focus {
  -webkit-animation: hinge 1s linear 0.8s;
          animation: hinge 1s linear 0.8s;
  box-shadow: 6px 6px 0px rgba(0, 0, 0, 0.3);
  outline: none;
}

.navigationList {
  position: fixed;
  right: 36px;
  top: 93px;
}

.navigationList ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.li_item {
  width: 32px;
  height: 32px;
  border: 1px solid #ce40b4;
  text-align: center;
  line-height: 2em;
  border-radius: 50%;
  margin-bottom: 6px;
}

.li_item .fas {
  font-size: 16px;
  color: #444;
}

.active {
  background: #ce40b4;
}
.active .fas {
  color: #fff;
}

.navigation__item {
  display: flex;
  width: 32px;
  height: 32px;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}

@media only screen and (max-width: 480px) {
  .navigationList {
    right: 24px;
    top: 85px;
  }

  .li_item,
  .navigation__item {
    width: 24px;
    height: 24px;
  }

  .li_item .fas {
    font-size: 12px;
  }
}

